@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
html{

  background-color: black;
  color: whitesmoke;
  background-attachment: fixed;
  scroll-behavior: smooth;
  font-family: 'Varela Round', sans-serif;
}

body {
  margin: 0px;
  padding: 0px;
}

body::-webkit-scrollbar {
  width: 15px
} 

body::-webkit-scrollbar-thumb {   
background-color: rgb(0, 0, 0);   
}



.aboutMe{height:auto;margin-left:50px;padding-top:55px;display:grid;grid-template-columns:repeat(2, 1fr)}.aboutMe .descricao{text-align:justify;margin:40px}.aboutMe .social{display:grid;grid-template-columns:repeat(3, 1fr)}.aboutMe .icon{font-size:50px;color:whitesmoke}.aboutMe .icon:hover{color:#03ee94}.aboutMe img{margin-left:80px}@media (max-width: 850px){.aboutMe{margin-left:0px;grid-template-columns:1fr;width:200vw}.aboutMe .descricao{text-align:justify;margin-left:20px;width:80vw}.aboutMe img{margin-left:20px}}

.skills{padding-top:40px;color:whitesmoke;font-size:30px;margin-left:60px}.skills ul{padding-top:40px;padding-bottom:40px}.skills .line{width:80vw}.skills .title{padding-top:40px}.skills .icons{display:grid;width:60vw;grid-template-columns:repeat(4, 1fr);font-size:50px}.skills .icons .icon{margin-top:30px}@media (max-width: 650px){.skills{margin-left:0px;font-size:35px;width:100vw}.skills .icons{width:100vw;grid-template-columns:repeat(3, 1fr)}.skills ul{width:100vw;font-size:20px}.skills ul li{margin-top:40px}}

.projects{height:400px;padding-top:40px;margin-bottom:400px;font-size:30px;margin-left:40px}.projects .title{padding-top:40px}.projects .line{width:80vw}.projects .conteiner{display:grid;grid-template-columns:repeat(2, 1fr);padding-bottom:200px}.projects .projetoPreview{text-align:center;margin:10px;border-radius:10px;width:550px;height:260px;font-size:20px;color:black}.projects .projetoPreview .projetoInfo{display:none;margin:0 auto;width:300px;height:250px}.projects .projetoPreview .projetoInfo .categoriaGrid{display:grid;grid-template-columns:repeat(3, 1fr)}.projects .projetoPreview .projetoInfo .categoriaGrid .categoria{margin:5px}.projects .projetoPreview .projetoInfo a{text-decoration:none;color:whitesmoke;background-color:black;border-radius:5px;padding:8px;margin-top:5px}.projects .projetoPreview .projetoInfo .bottom{margin-bottom:30px}.projects .projetoPreview .projetoInfo a:hover{border:solid 1px  black;box-shadow:0 0 1em #0f8;color:black;background-color:white}.projects .projetoPreview:hover{cursor:pointer;background-image:linear-gradient(to bottom right, rgba(255,255,255,0.2), rgba(255,255,255,0.176471)) !important;border:4px solid black}.projects .projetoPreview:hover .projetoInfo{display:block;color:#000;padding:5px;border-collapse:collapse;background-color:whitesmoke;-webkit-filter:none;filter:none}@media (max-width: 900px){.projects .conteiner{margin:0;grid-template-columns:1fr}.projects .projetoPreview{margin:10px}}

.menu{background-color:whitesmoke;text-align:end;padding:15px;font-size:18px;position:fixed;width:98%}.menu a{text-decoration:none;color:black;padding:15px}.menu a:hover{background-color:black;color:whitesmoke}@media (max-width: 350px){.menu{font-size:35px}.menu a{text-align:center;border:1px solid black;padding-right:10px}}

